<template>
	<div>
		<ContentHeader title="Perawatan" url="/perawatan" subTitle="Perawatan Pasien" />
		<div class="content-header">
			<div class="container-fluid">
				<div class="row justify-content-center">
		            <div class="col">
		            	<div class="card">
		            		<div class="card-header row">
		            			<h3 class="card-title">Edit Data Pemeriksaan</h3>
		            		</div>
		            		<div class="card-body">
		            			<form @submit.prevent="prosesEditData">
					                <div class="px-lg-2 ">
					                  <div class="form-group">
					                    <label for="nomor_rekam_medis">No Rekam Medis</label>
					                    <input type="text" class="form-control" id="nomor_rekam_medis" name="nomor_rekam_medis" disabled="" v-model="rekam_medis.nomor_rekam_medis">
					                  </div>
					                  <div class="form-group">
					                    <label for="anamnesa">Anamnesa <span class="text-danger">*</span></label>
					                    <textarea class="form-control" id="anamnesa" required="" name="anamnesa" v-model="rekam_medis.anamnesa"></textarea>
					                  </div>
					                  <div class="form-group">
					                    <label for="diagnosa">Diagnosa <span class="text-danger">*</span></label>
					                    <textarea class="form-control" id="diagnosa" required="" name="diagnosa" v-model="rekam_medis.diagnosa"></textarea>
					                  </div>
					                  <div class="form-group">
					                    <label for="resep_obat">Resep Obat <span class="text-danger">*</span></label>
					                  	<select class="custom-select form-control" id="resep_obat" name="resep_obat" required="" v-model="rekam_medis.resep_id">
      										<option selected></option>
	      									<option v-for="row in resep" :key="row.id" v-bind:value="row.id">{{ row.kode }} - {{ row.nama }}</option>
	    								</select>
					                  </div>
					                  <div class="form-group">
					                    <label for="tindakan">Tindakan <span class="text-danger">*</span></label>
					                  	<select class="custom-select form-control" id="tindakan" name="tindakan" required="" v-model="rekam_medis.tindakan_id">
      										<option selected></option>
	      									<option v-for="row in tindakan" :key="row.id" v-bind:value="row.id">{{ row.kode_tindakan }} - {{ row.nama }}</option>
	    								</select>
					                  </div>
					                  <button type="submit" class="btn btn-primary mt-3">Submit</button>
					                </div>
					                <!-- /.card-body -->					               
					              </form>
		            		</div>
		            	</div>
		            </div>
		        </div>
			</div>
		</div>
	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader2'

	import { useRoute } from 'vue-router'
	import { ref, onMounted, computed } from 'vue'
	import axios from 'axios'
	import Swal from 'sweetalert2'
	import router from '@/router'
	import store from '@/store'

	export default{
		components: {
			ContentHeader: ContentHeader
		},
		setup(){
			const user = computed(() => store.getters['auth/user'])
			const route = useRoute()
			const kunjungan_id = route.params.id
			const rekam_medis = ref([])

			const getRekamMedis = async () => {
				let response = await axios.get(`api/rekam_medis/getWhereId/${kunjungan_id}`)
				rekam_medis.value = response.data
				console.log(rekam_medis.value)
			}

			// ---------Get data tindakan & resep obat---------
			const resep = ref([])
			const tindakan = ref([])

			const getTindakan = async () => {
				let response = await axios.get(`api/tindakan/get/${user.value.cabang_id}`)
				tindakan.value = response.data
			}

			const getResep = async () => {
				let response = await axios.get(`api/resep_obat/get/${user.value.cabang_id}`)
				resep.value = response.data
			}

	    	// ---------End Get data tindakan & resep obat---------

			const prosesEditData = () => {
				axios.put(`api/rekam_medis/update/${rekam_medis.value.id}`, rekam_medis.value)
				.then((response) => {
					Swal.fire({
					  title: 'Berhasil!',
					  text: 'Berhasil mengedit data',
					  icon: 'success',
					  confirmButtonText: 'Ok'
					})	
		    		console.log(response.data)
		    	})
		    	.catch((error) => {
		    		Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal mengedit data',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})	
		    		console.log(error)
		    	})	

		    	router.push('/perawatan')
			}

			onMounted(() => {
				getRekamMedis()
				getTindakan()
				getResep()
			})

			return { rekam_medis, getRekamMedis, prosesEditData, getTindakan, getResep, tindakan, resep, user }
		}	
	}
</script>